import React from 'react'
import { Card } from 'react-bootstrap'

function Textbox () {
  return (
    <div className='CardDeck'>
      <Card style={{ width: '18rem' }} className='card'>
        <Card.Body className='cardBody'>
          <Card.Title
            style={{
              fontFamily: "'Montserrat', sans-serif",
              fontWeight: '600',
              fontSize: '120%',
              marginBottom: '10%'
            }}
					>
            {'\u00A0'}
						Changelogs:
					</Card.Title>
          <div className='card2'>
            <Card.Text>
							â€¢ Release v1.0.0+1 <br />
              <br />
              {'\u00A0'}Date: 22 May 2021
						</Card.Text>
          </div>
        </Card.Body>
      </Card>
      <a
        href='https://drive.google.com/file/d/10kBzGwFNBYSI9_2h0F5tBu4XYyyOL4Xb/view?usp=sharing'
        className='button1'
        target='_blank'
        rel='noopener noreferrer'
			>
				DOWNLOAD LATEST APK
			</a>
    </div>
  )
}

export default Textbox
